.btn-primary, .btn-primary:hover, .btn-primary:active, .btn-primary:visited{
    background-color: #FF8DA1 !important;
    border: #E782A9 !important;
}

.btn-primary.focus, .btn-primary:focus{
    box-shadow: 0 0 0 0.2rem rgb(231 130 169 / 50%) !important;
}

.btn-danger, .btn-danger:hover, .btn-danger:active, .btn-danger:visited{
    background-color: #E24585 !important;
    border: #D00070 !important;
}

.swal-button{
    background-color: #FF8DA1 !important;
}
