/*
Diseño flechas
*/

#slide{
   width: 100%;
   height: 100%;
}
#slide .flechas{
	position: absolute;
	font-size:2em;
	color:white;
	cursor:pointer;
	z-index:2;
}

#slide #retroceder{
	background:#ffc0cb;
	left:0;
	opacity: 0.5;
    bottom: 5px
}

#slide #avanzar{
	background:#ffc0cb;
	right:0;
	opacity: 0.5;
    bottom: 5px
}

.tamFlech{
    width: 80px;
    height: 80px;
}


#slide #retroceder:hover, #slide #avanzar:hover{
	background:#777;
}

#slide #retroceder:active, #slide #avanzar:active{
	background:#444;
}

.celularBook{
    position: absolute;
    left: 0;
}

